<template>
  <div id="app">
    <el-row>
      <el-col :span="24">
        <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect"
          background-color="#545c64" text-color="#fff" active-text-color="#ffd04b">
          <el-menu-item index="/">Home</el-menu-item>
          <el-submenu index="/web">
            <template slot="title">Web</template>
            <el-menu-item index="/html">Html</el-menu-item>
            <el-menu-item index="/css">Css</el-menu-item>
            <el-menu-item index="/javascript">Javascript</el-menu-item>
          </el-submenu>
          <el-menu-item index="/server" disabled>Server</el-menu-item>
          <el-menu-item index="/linux">Linux</el-menu-item>
        </el-menu>
      </el-col>
    </el-row>
    <router-view></router-view>
    <div class="footer">
      FOOTER
    </div>
    <el-backtop></el-backtop>
  </div>
</template>
<script>
  export default {
    data() {
      return {
        activeIndex: '/'
      };
    },
    methods: {
      handleSelect(key, keyPath) {
        // console.log(key, keyPath);
        var route = '';
        for (var i = 0; i < keyPath.length; i++) {
          route += keyPath[i];
        }
        this.activeIndex = key;
        this.$router.push({
          path: route
        });
      }
    }
  }
</script>



