<template>
  <div class="details">
    <el-row :gutter="20">
      <el-col :span="16">
        title:{{data.title}}
        <hr />
        author:{{data.author}}
        source:{{data.source}}
        view:{{data.view}}
        create_time:{{data.create_time | formatDate}}
        <hr />
        description:{{data.description}}
        <hr />
        content:<p v-html="data.content"></p>
      </el-col>
      <el-col :span="8">
        <div class="grid-content bg-purple"></div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        data: 'Details_Page'
      }
    },
    created() {
      let id = this.$route.query.id;
      // console.log(id);
      this.$axios.get('details?id=' + id)
        .then(res => {
          // console.log(res);
          this.data = res.data;
        })
        .catch(err => {
          console.log(err);
        })
    },
    filters: {
      formatDate(value) {
        let date = new Date(value * 1000);
        let y = date.getFullYear();
        let MM = date.getMonth() + 1;
        MM = MM < 10 ? ('0' + MM) : MM;
        let d = date.getDate();
        d = d < 10 ? ('0' + d) : d;
        let h = date.getHours();
        h = h < 10 ? ('0' + h) : h;
        let m = date.getMinutes();
        m = m < 10 ? ('0' + m) : m;
        let s = date.getSeconds();
        s = s < 10 ? ('0' + s) : s;
        return y + '-' + MM + '-' + d + ' ' + h + ':' + m + ':' + s;
      }
    }
  }
</script>

