<template>
    <div class="server">
        {{data}}
    </div>
</template>

<script>
  export default {
    data() {
      return {
        data: ''
      }
    },
    created() {
      this.$axios.get('server')
        .then(res => {
          // console.log(res);
          this.data = res.data;
        })
        .catch(err => {
          console.log(err);
        })
    }
  }
</script>

<!-- 添加“scoped”属性以仅将CSS限制到此组件 -->



