<template>
  <div class="home">
    <el-row :gutter="20">
      <el-col :span="15">
        <div class="slider">
          <el-carousel trigger="click">
            <el-carousel-item v-for="item in slider" :key="item.id">
              <img v-lazy="baseURL+item.src">
            </el-carousel-item>
          </el-carousel>
        </div>
        <div class="list">
          <el-col :span="8" v-for="item in lists" :key="item.id" class="list_item">
            <el-card :body-style="{ padding: '0px' }">
              <router-link :to="{name:'details',query:{id:item.id}}">
                <img v-lazy="item.thumb" class="image">
                <div style="padding: 10px;">
                  <span class="title">{{item.title}}</span>
                  <div class="bottom clearfix">
                    <time class="time">{{ item.create_time|formatDate }}</time>
                  </div>
                </div>
              </router-link>
            </el-card>
          </el-col>
        </div>
      </el-col>
      <el-col :span="9">
        <el-col :span="18">
          <el-input placeholder="请输入内容" v-model="input" clearable>
          </el-input>
        </el-col>
        <el-col :span="6">
          <el-button type="primary" icon="el-icon-search" @click="search">搜索</el-button>
        </el-col>
        <el-col :span="24">
          <el-card class="box-card">
            <div slot="header">
              <span>最新文章</span>
            </div>
            <div v-for="item in news" :key="item.id" class="text item">
              <router-link :to="{name:'details',query:{id:item.id}}">{{item.title}}</router-link>
            </div>
          </el-card>
          <el-card class="box-card">
            <div slot="header">
              <span>浏览排行</span>
            </div>
            <div v-for="item in views" :key="item.id" class="text item">
              <router-link :to="{name:'details',query:{id:item.id}}">{{item.title}}</router-link>
            </div>
          </el-card>
          <el-card class="box-card">
            <div slot="header">
              <span>标签</span>
            </div>
            <div>
              <el-tag v-for="item in tags" :key="item.id">
                {{item.tag}}
              </el-tag>
            </div>
          </el-card>
        </el-col>
      </el-col>
    </el-row>
    <el-row :gutter="20" class="links">
      <el-col :span="24">
        <el-link v-for="item in links" :href="item.href" target="_blank" :key="item.id">{{item.title}}</el-link>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        baseURL: '',
        slider: '',
        lists: '',
        input: '',
        news: '',
        views: '',
        tags: '',
        links: ''
      }
    },
    created() {
      this.baseURL = this.$axios.defaults.baseURL;
      var slider = this.$axios.get('slider');
      var lists = this.$axios.get('lists');
      var news = this.$axios.get('news');
      var views = this.$axios.get('views');
      var tags = this.$axios.get('tags');
      var links = this.$axios.get('links');
      this.$axios.all([slider, lists, news, views, tags, links])
        .then(this.$axios.spread((slider, lists, news, views, tags, links) => {
          this.slider = slider.data;
          this.lists = lists.data;
          this.news = news.data;
          this.views = views.data;
          this.tags = tags.data;
          this.links = links.data;
        }))
        .catch(err => {
          console.log(err);
        })
    },
    methods: {
      search() {
        console.log(this.input);
      }
    },
    filters: {
      formatDate(value) {
        let date = new Date(value * 1000);
        let y = date.getFullYear();
        let MM = date.getMonth() + 1;
        MM = MM < 10 ? ('0' + MM) : MM;
        let d = date.getDate();
        d = d < 10 ? ('0' + d) : d;
        let h = date.getHours();
        h = h < 10 ? ('0' + h) : h;
        let m = date.getMinutes();
        m = m < 10 ? ('0' + m) : m;
        let s = date.getSeconds();
        s = s < 10 ? ('0' + s) : s;
        return y + '-' + MM + '-' + d + ' ' + h + ':' + m + ':' + s;
      }
    }
  }
</script>

<!-- 添加“scoped”属性以仅将CSS限制到此组件 -->
<style scoped>
  .home {
    margin-top: 20px;
  }

  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 150px;
    margin: 0;
  }

  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }

  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }

  .box-card {
    margin-top: 20px;
  }

  .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }

  .el-tag {
    margin: auto 5px 5px 5px;
  }

  .el-link {
    padding: 5px;
  }

  .el-card__body .item {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .list {
    margin-top: 20px;
  }

  .list_item {
    margin-top: 10px;
  }

  .el-card__body .item a {
    font-size: 16px;
    color: #333333;
  }

  .time {
    font-size: 13px;
    color: #999;
  }

  .bottom {
    margin-top: 13px;
    line-height: 12px;
    text-align: right;
  }

  .image {
    width: 100%;
    height: 225px;
    display: block;
  }

  .el-card__body .title {
    color: #666666;
    display: block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }

  .clearfix:after {
    clear: both
  }

  .links {
    margin: 20px auto;
  }
</style>



