import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home/Home'
import Web from '@/components/Web/Web'
import Html from '@/components/Web/Html'
import Css from '@/components/Web/Css'
import Javascript from '@/components/Web/Javascript'
import Server from '@/components/Server/Server'
import Linux from '@/components/Linux/Linux'
import Details from '@/components/Details/Details'

Vue.use(Router)

export default new Router({
  routes: [{
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/web',
      name: 'web',
      component: Web,
      children: [{
        path: 'html',
        name: 'html',
        component: Html
      }, {
        path: 'css',
        name: 'css',
        component: Css
      }, {
        path: 'javascript',
        name: 'javascript',
        component: Javascript
      }]
    },
    {
      path: '/server',
      name: 'server',
      component: Server
    },
    {
      path: '/linux',
      name: 'linux',
      component: Linux
    },
    {
      path: '/details',
      name: 'details',
      component: Details
    }
  ],
  mode: 'history' //去除#号
})



